@use 'partials' as *;

*, *:before, *:after{
    box-sizing: border-box;
}
html{
    font-size: 10px;
}
body, h1, h2, h3, h4, h5, h6{
    margin: 0;
}
body{
    display: grid;
    place-items: center;
    min-height: 100vh;
    font-size: 1.6rem;
    font-family: 'Red Hat Display', sans-serif;
    overflow-y: hidden;
}

.container{
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: space-between;
    width: 23.5%;
    // border: 1px solid #eee;
    border-radius: 1.6rem;
    box-shadow: 0rem .5rem .5rem rgba(0, 0, 0, .2);
    // background-color: red;
    

    .top{
        width: 100%;
        img{
            border-radius: 1.6rem 1.6rem 0 0;
            width: 100%;

        }
    }

    .bottom{
        padding: 1rem;
        width: 80%;
        
      

       .bottom-text, .bottom-payment-cancel, .payment, .cancel{
            margin-bottom: 2rem;
       }
       .bottom-album{
           margin-bottom: 2.5rem;
       }
        .bottom-text{
            margin-top: 2rem;
            text-align: center;

            h1{
                color: $dark-blue;
                font-weight: 900;
            }
            p{
                color: $desaturadted-blue;
                font-weight: 500;
                font-size: 1.6rem;
                line-height: 1.5;
            }
        }

        .bottom-album{
            display: flex;
            justify-content: space-between;
            align-items: center;
            background: $very-pale-blue;
            padding: 2rem;
            border-radius: 1.5rem;

          
           
            img{
                margin-right: 2rem;
            }

            h4{
                color: $dark-blue;
                font-weight: 700;
            }
            h5{
                color: $desaturadted-blue;
                font-weight: 500;
            }

          a{
              margin-left: auto;
              color: $bright-blue;
              font-weight: 500;
          }

          a:active{
              text-decoration: none;
          }
        }

        .bottom-payment-cancel{

            .payment{
                display: block;
                position: relative;
                background: $bright-blue;
                color: $very-pale-blue;
                text-align: center;
                // vertical-align: center;
                line-height: 5rem;
                height: 4.5rem;
                font-weight: 700;
                border-radius: 1rem;
                text-decoration: none;

                &:after{
                    content: "";
                    position: absolute;
                    top: 100%;
                    left: 0;
                    filter: blur(1.3rem);
                    background: $desaturadted-blue;
                    width: 100%;
                    height: 20%;

                }

                &:active{
                    opacity: .75;
                }
            }

            .cancel{
                display: block;
                background: #fff;
                color: $desaturadted-blue;
                text-align: center;
                font-weight: 700;
                height: 4.5rem;
                line-height: 5rem;
                text-decoration: none;



                &:active{
                    color: $dark-blue;
                }
            }
        }
    }
}

@media screen and (min-width: 1550px) and (max-width: 1670px) {
     .container{
         width: 25%;
     }
}

@media screen and (min-width: 1300px) and (max-width: 1550px) {
    .container{
        width: 30%;
    }
}
@media screen and (min-width: 1100px) and (max-width: 1300px) {
    .container{
        width: 35%;
    }
}

@media screen and (min-width: 990px) and (max-width: 1100px) {
    .container{
        width: 40%;
    }
}
@media screen and (min-width: 870px) and (max-width: 990px){
    .container{
        width: 45%;
    }
}

@media screen and(min-width: 770px) and (max-width: 870px) {
    .container{
        width: 50%;
    }
}

@media screen and (min-width: 650px) and (max-width: 770px) {
    .container{
        width: 60%;
    }
}
@media screen and(min-width: 550px) and  (max-width: 650px) {
    .container{
        width: 70%;
    }
}

@media screen and (min-width: 490px) and (max-width: 550px) {
    .container{
       
        width: 75%;

        .bottom{
            width: 90%;
        }
    }
}
@media screen and (min-width: 400px) and (max-width: 490px) {
    .container{
       
        width: 90%;
        
        .bottom{
            width: 90%;
        }
    }
}

@media screen and (max-width: 400px) {
    .container{
       
        width: 95%;
        
        .bottom{
            width: 95%;
        }
    }
}